import React from "react";
import styled from "styled-components";
import { useHistory, useLocation } from "react-router-dom";
import { FaList, FaChartArea, FaInfoCircle } from "react-icons/fa";

const Container = styled.nav`
  background: #646464;
  height: 50px;
  width: 100%;
  box-shadow: 0px -1px 3px #00000073;
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 2;
  bottom: 0;
`;

interface NavItemType {
  active?: boolean | undefined;
}

const NavItem = styled.div<NavItemType>`
  background: ${({ active }) => (active ? "#5d5d5d" : "#C4C4C4")};
  width: 33.33%;
  height: 100%;
  border-right: 1px solid #9a9a9a;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 350ms ease-in-out;
  cursor: pointer;

  svg {
    width: 24px;
    height: 24px;
    color: ${({ active }) => (active ? "#fff" : "#525252")};
  }

  :last-child {
    border-right: none;
  }
`;

const Footer = () => {
  const { push } = useHistory();

  const { pathname } = useLocation();

  return (
    <Container>
      <NavItem onClick={() => push("/")} active={pathname === "/"}>
        <FaList />
      </NavItem>
      <NavItem onClick={() => push("/charts")} active={pathname === "/charts"}>
        <FaChartArea />
      </NavItem>
      <NavItem onClick={() => push("/info")} active={pathname === "/info"}>
        <FaInfoCircle />
      </NavItem>
    </Container>
  );
};

export default Footer;
