import React from "react";
import { ApolloProvider, useQuery } from "@apollo/client";
import { BrowserRouter as Router, Route } from "react-router-dom";
import client from "./apollo";
import { InformesType } from "./apollo/types";
import { GET_INFORMES } from "./apollo/queries/InformeQueries";
import styled from "styled-components";
// import Chart from "./components/chart/Chart";
import MyChart from "./components/chart/Chart2";
import List from "./components/list/List";
import Navbar from "./components/shared/Navbar";
import Footer from "./components/shared/Footer";

const Container = styled.div`
  display: flex;
  /* width: 100vw; */
  height: auto;
  flex-wrap: wrap;
  overflow-x: hidden;
  /* background-image: url("./bg-image.jpg"); */

  :after {
    content: "";
    background: url("./img/bg-image.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: top;
    background-size: cover;
    opacity: 0.3;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }

  div.single-informe {
    margin: 10px 20px;

    ul {
      padding: 0;
      list-style: none;

      b {
        margin-right: 10px;
      }
    }
  }

  g {
    text {
      tspan {
        font-size: 6px !important;
      }
    }
  }
`;

function App(): JSX.Element {
  const { data } = useQuery<InformesType>(GET_INFORMES, {
    client,
  });

  return (
    <div className="App">
      <ApolloProvider client={client}>
        <Router>
          <Container>
            <Navbar />
            {data?.getInformes && (
              <>
                <Route
                  exact
                  path="/"
                  render={(props) => (
                    <List {...props} informes={data?.getInformes} />
                  )}
                />
                <Route
                  path="/charts"
                  render={(props) => (
                    <MyChart {...props} informes={data?.getInformes} />
                  )}
                />
              </>
            )}
            <Footer />
          </Container>
        </Router>
      </ApolloProvider>
    </div>
  );
}

export default App;
