import React from "react";
import styled from "styled-components";

const Container = styled.nav`
  background: #646464;
  height: 34px;
  width: 100%;
  box-shadow: 0px 1px 3px #00000073;

  padding: 0px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 2;
  box-sizing: border-box;
`;

const Logo = styled.span`
  font-family: Satisfy;
  font-size: 24px;
  color: #fff;
`;

const Status = styled.div`
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background: #71cc51;
  box-shadow: 1px 1px 3px #0000008c;
  opacity: 0;
  animation: 1.5s fadeIn infinite;
`;

const Navbar = () => {
  return (
    <Container>
      <Logo>congovid</Logo>
      <Status />
    </Container>
  );
};

export default Navbar;
