import React from "react";
import { Chart } from "react-charts";
import moment from "moment";
import { InformeType } from "../../apollo/types";
import styled from "styled-components";

moment.locale("pt-br");

const Container = styled.div`
  width: 100%;
  height: calc(100vh - 200px);
  margin: 10px;
  margin-top: 50px;
  overflow: hidden;

  .legenda {
    display: flex;
    justify-content: center;
    font-weight: 600;
    margin: 10px;
    .box {
      padding: 10px;
      margin-right: 10px;
    }
  }
`;

function MyChart({ informes }) {
  informes = informes.slice().reverse();
  const data = React.useMemo(
    () => [
      {
        label: "Mortes",
        data: informes.map((inf: InformeType) => [
          new Date(Number(inf.dia)).toLocaleDateString("pt-BR"),
          inf.obto_confirmado,
        ]),
      },
      {
        label: "Monitoramento",
        data: informes.map((inf: InformeType) => [
          new Date(Number(inf.dia)).toLocaleDateString("pt-BR"),
          inf.monitoramento,
        ]),
      },
      {
        label: "Casos",
        data: informes.map((inf: InformeType) => [
          new Date(Number(inf.dia)).toLocaleDateString("pt-BR"),
          inf.confirmados,
        ]),
      },
    ],
    [informes]
  );

  const series = React.useMemo(
    () => ({
      type: "area",
    }),
    []
  );

  // const axes = React.useMemo(
  //   () => [
  //     { type: "ordinal", position: "left", primary: true },
  //     { type: "linear", stacked: false, position: "bottom" },
  //   ],
  //   []
  // );

  const axes = React.useMemo(
    () => [
      { primary: true, position: "bottom", type: "ordinal" },
      { position: "left", type: "linear", stacked: false },
    ],
    []
  );

  return (
    <Container>
      <Chart data={data} series={series} axes={axes} tooltip />
      <div className="legenda">
        <div className="box" style={{ background: "#decf3f", color: "black" }}>
          Casos
        </div>
        <div className="box" style={{ background: "#fc6868", color: "white" }}>
          Monitoramento
        </div>
        <div className="box" style={{ background: "#77bee6", color: "white" }}>
          Mortes
        </div>
      </div>
    </Container>
  );
}

export default MyChart;
