import React from "react";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 35px;
  overflow: auto;
  height: calc(100vh - 85px);
`;

const List = ({ informes }) => {
  return (
    <Container>
      {informes.map((d) => {
        const {
          confirmados,
          href,
          id,
          monitoramento,
          obto_confirmado,
          obto_descartado,
          obto_investigacao,
          recuperados,
          dia,
        } = d;

        return (
          <div key={id} className="single-informe">
            <h3>{new Date(Number(dia)).toLocaleDateString("pt-BR")}</h3>
            <ul>
              <li>
                <b>Confirmados</b>
                {confirmados}
              </li>
              <li>
                <b>Monitoramento</b>
                {monitoramento}
              </li>
              <li>
                <b>Óbitos confirmados </b>
                {obto_confirmado}
              </li>
              <li>
                <b>Óbitos descartados</b>
                {obto_descartado}
              </li>
              <li>
                <b>Óbitos investigação</b>
                {obto_investigacao}
              </li>
              <li>
                <b>Recuperados</b>
                {recuperados}
              </li>
              <li>
                <a href={href}>Link pdf</a>
              </li>
            </ul>
          </div>
        );
      })}
    </Container>
  );
};

export default List;
